import { Grid, ResponsiveContext } from "grommet"
import React, { useContext } from "react"
import Img from "gatsby-image"
import { graphql } from "gatsby"

import styled from "styled-components"
import SEO from "../components/seo"

const Panel = styled.div`
  background: #c4c4c4;
  width: 100%;
  height: 100%;
  flex: 0 0 100%;

  display: flex;
  justify-content: center;

  transition: 0.3s ease-in;
`;

const Slide = styled.div`
  width: 100%;
  height: 100%;
  flex: 0 0 100%;
  padding: 10px;
  background: black;
  color: white;
  transition: 0.3s ease-in;
`;

const SlideWrapper = styled.div`
  display: flex;
  overflow-x: hidden;

  &:hover {
    > div:first-child {
      opacity: 0;
    }

    > div:last-child {
      transform: translatex(-100%);
    }
  }
`;

const SlideCard = ({img}) => (
  <SlideWrapper>
    <Panel>
      <Img fixed={img}/>
    </Panel>
    <Slide>
      <h3>Work In Progress</h3>
      <p>I still working on the projects!</p>
    </Slide>
  </SlideWrapper>
)

const ProjectPage = ({data}) => {
  const size = useContext(ResponsiveContext)
  return (
  <>
    <SEO title="Home"/>
    <Grid 
    columns={ 
      size === 'small' 
      ? {
          count: 1,
          size: 'auto',
        } 
      : { 
          count: 3,
          size: 'auto',
      }
    }
    gap='medium'
    rows={['350px']}
    >
      <SlideCard img={data.project1.childImageSharp.fixed}/>
      <SlideCard />
      <SlideCard />
    </Grid>
  </>
  )
}


export default ProjectPage

export const query = graphql`
  query {
    project1: file(relativePath: { eq: "chatterbox.png" }) {
      childImageSharp {
        fixed(
          height: 350
          ) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`